import {Component} from "@angular/core";

import {ShareButtons} from "../share/share.component";

@Component({
  selector: "customize",
  template: require("./customize.html"),
  directives: [ShareButtons]
})

export class Customize {

  shareTitle = "Custom share icons";

  fbInner = "<img src=" + require('../../assets/img/custom/facebook.svg') + ">";
  twitterInner = "<img src=" + require('../../assets/img/custom/twitter.svg') + ">";
  pintInner = "<img src=" + require('../../assets/img/custom/pinterest.svg') + ">";
  inInner = "<img src=" + require('../../assets/img/custom/linkedin.svg') + ">";
  googleInner = "<img src=" + require('../../assets/img/custom/google-plus.svg') + ">";
  tumblrInner = "<img src=" + require('../../assets/img/custom/tumblr.svg') + ">";
}



